.mindSection{
    color: var(--primary-text-color);
    padding: 60px 72px 0px 72px;
    margin-right: auto;
    margin-left: auto;
    @media screen and (min-width: 768px) {
    padding: 80px 88px 0px 88px;
    margin-right: auto;
    margin-left: auto;
}

    @media screen and (min-width: 1280px){
        padding: 120px 124px 0 124px;
    }
}

.mindTitle{
    font-family: "Right Grotesk";
    font-size: 44px;
    font-weight: 900;
    line-height: 0.90909;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 24px;

    @media screen and (min-width: 768px) {
        font-size: 80px;
        line-height: 1;
        margin-bottom: 40px;
        }
    @media screen and (min-width: 1280px){
    font-size: 160px;
    margin-bottom: 80px;
    }
}

.textBox{
    display: flex;
    flex-direction: column;   
    font-family: "Messina Sans Mono";
    font-size: 12px;
    line-height: 1.1667;
    text-transform: uppercase;
    flex-grow: 1;

    @media screen and (min-width: 768px) {
    margin-left: auto;
    }

    @media screen and (min-width: 1280px){
    font-size: 24px;
    line-height: 1.20833;
    }
}

.boxSwiper{
    width: 216px;
}

.cardBox{
  padding: 24px 12px;
  width: 216px;
  height: 242px;
  display: flex;
  flex-direction: column;
  border-radius: 12px;
  margin-bottom: 24px;
  background-color: var(--secondary-background);

@media screen and (min-width: 768px) {
        width: 284px;
        padding: 24px;
        margin-bottom: 0;
    }

    @media screen and (min-width: 1280px) {
    width: 504px;
    height: 480px;
    }
}

.cardBox.active {
    background-color: var(--background-red);
}

.vectorBox {
    position: relative;
    margin-left: auto;
}


.cardTitle{
    font-family: "Right Grotesk";
    font-size: 32px;
    font-weight: 900;
    line-height: 1;
    text-transform: uppercase;

    @media screen and (min-width: 1280px){
    font-size: 62px;
    }
}

.btnBox{
    display: flex;
    gap: 48px;
    justify-content: center;
}

.sliderBtn{
    all: initial;
    color: var(--primary-text-color);
    font-family: "Biro Script Plus";
    font-size: 24px;
    line-height: 1;
    background-color: transparent;
    cursor: pointer;
    transition: color 250ms var(--timing-function);
     
    &:hover,
        &:focus {
            color: var(--accent-color);
        }
    
}

/*for tablet, desctop*/
.mindContainer{
    display: grid;
    justify-content: center;
    align-content: center;
    grid-template-columns: repeat(2, minmax(0, auto));
    grid-template-rows: auto auto;
    gap: 24px;
}

